import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/pc',
    name: 'Index',
    component: () => import(/* webpackChunkName: "about" */ '../views/PC/Index.vue'),
    children: [
      {
        path: 'home',
        name: 'Home',
        component: () => import(/* webpackChunkName: "about" */ '../views/PC/Home.vue')
      },
      {
        path: 'stake',
        name: 'Stake',
        component: () => import(/* webpackChunkName: "about" */ '../views/PC/Stake.vue')
      }
    ]
  },
  {
    path: '/h5',
    name: 'Index2',
    component: () => import(/* webpackChunkName: "about" */ '../views/H5/Index.vue'),
    redirect: '/h5/home',
    children: [
      {
        path: 'home',
        name: 'Home2',
        component: () => import(/* webpackChunkName: "about" */ '../views/H5/Home.vue')
      },
      {
        path: 'stake',
        name: 'Stake2',
        component: () => import(/* webpackChunkName: "about" */ '../views/H5/Stake.vue')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
