import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/common.scss'
import VueI18n from 'vue-i18n'
import en from './assets/i18n/en'
import cn from './assets/i18n/cn'
import alabo from './assets/i18n/alabo'

import simpleTokenABI from './assets/js/simpleToken.js'
import hosTokenABI from './assets/js/Hos.js'
import axios from 'axios'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

Vue.prototype.$axios = axios

window.simpleTokenABI = simpleTokenABI
window.hosTokenABI = hosTokenABI

Vue.use(VueI18n)

const messages = {
  en: en,
  cn: cn,
  alabo: alabo
}

const i18n = new VueI18n({
  locale: 'en', 
  messages
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
